.home {
  max-width: 144rem;
  grid-template-rows: 1fr 2fr;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  column-gap: 2.4rem;
  margin: 0 auto;
  display: grid;
}

.home-nav {
  border-bottom: 1px solid #fff;
  padding-bottom: .5rem;
  font-weight: 700;
}

.home-text {
  grid-column: span 3;
  font-size: 3rem;
  font-weight: 400;
  line-height: 3.6rem;
}

.menu-link {
  cursor: pointer;
  border-bottom: .1rem solid #fff;
  padding-bottom: .5rem;
}

.home-img-box {
  max-width: 53.7rem;
}

.home-img {
  width: 100%;
}

.hidden {
  display: none;
}

.close-modal {
  color: #717171;
  cursor: pointer;
  background: none;
  border: none;
  font-family: Barlow, sans-serif;
  font-size: 2.4rem;
  position: absolute;
  top: 4.4rem;
  right: 6.4rem;
}

.modal {
  max-width: 50%;
  z-index: 10;
  background-color: #fff;
  padding: 8rem;
  font-size: 3rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.flex-div {
  flex-direction: column;
  gap: 4rem;
  display: flex;
}

.menu-title {
  color: #1c1c1c;
  align-self: center;
  font-size: 3rem;
}

.menu-list {
  flex-direction: column;
  gap: 1.6rem;
  display: flex;
}

.menu-item-name {
  color: #1c1c1c;
  font-size: 2.4rem;
  text-decoration: none;
  list-style: none;
}

.menu-item-description {
  color: #1c1c1c;
  margin-bottom: 2.4rem;
  font-size: 2rem;
  text-decoration: none;
  list-style: none;
}

.overlay {
  width: 100%;
  height: 100%;
  z-index: 5;
  background-color: #0009;
  position: fixed;
  top: 0;
  left: 0;
}

